<template>
  <div>
    <el-form label-width="100px" size="mini">
      <el-form-item label="">
        <el-input v-model="attribute.chartTitle"/>
      </el-form-item>
      <el-form-item label="(X)">
        <el-input v-model="attribute.titleX"/>
      </el-form-item>
      <el-form-item label="(Y)">
        <el-input v-model="attribute.titleY"/>
      </el-form-item>
      <el-form-item label="">
        <el-input-number v-model="attribute.titleFontSize" :min="10" :max="50"/>
      </el-form-item>
      <el-form-item label="">
        <el-color-picker v-model="attribute.titleTextColor"/>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="attribute.subtext"/>
      </el-form-item>
      <el-form-item label="">
        <el-color-picker v-model="attribute.subtextColor"/>
      </el-form-item>
      <el-form-item label="">
        <el-switch v-model="attribute.legendShow" active-text="" inactive-text=""/>
      </el-form-item>
      <div v-show="attribute.legendShow">
        <el-form-item label="(X)">
          <el-input v-model="attribute.legendX"/>
        </el-form-item>
        <el-form-item label="(Y)">
          <el-input v-model="attribute.legendY"/>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="attribute.orient">
            <el-option label="" value="vertical"/>
            <el-option label="" value="horizontal"/>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input-number v-model="attribute.legendFontSize" :min="2" :max="120"/>
        </el-form-item>
        <el-form-item label="">
          <el-color-picker v-model="attribute.legendTextColor"/>
        </el-form-item>
      </div>
      <el-form-item label="">
        <el-select v-model="attribute.roseType">
          <el-option label="" value="false"/>
          <el-option label="" value="radius"/>
          <el-option label="" value="area"/>
        </el-select>
      </el-form-item>
      <el-form-item label="%">
        <el-input-number v-model="attribute.radiusOutside" :min="2" :max="120"/>
      </el-form-item>
      <el-form-item label="%">
        <el-input-number v-model="attribute.radiusInside" :min="0" :max="100"/>
      </el-form-item>
      <el-form-item label="">
        <el-slider v-model="attribute.borderRadius" :min="0" :max="120"/>
      </el-form-item>
      <el-form-item label="">
        <div>
          <div v-for="(item,index) in attribute.pieColor" :key="index" class="colorBlock" :style="{backgroundColor:item}">
            <div class="delTag" @click="delColor(index)">
              <i class="el-icon-delete"></i>
            </div>
          </div>
          <el-color-picker v-model="tempColor" show-alpha @change="addColor"/>
        </div>
      </el-form-item>
      <el-form-item label="label">
        <el-select v-model="attribute.labelPosition">
          <el-option label="" value="inside"/>
          <el-option label="" value="outside"/>
          <el-option label="" value="center"/>
        </el-select>
      </el-form-item>
      <el-form-item label="label">
        <el-input-number v-model="attribute.labelFontSize" :min="2" :max="120"/>
      </el-form-item>
      <el-form-item label="label">
        <el-color-picker v-model="attribute.labelColor"/>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "cpt-chart-pie-option",
  props: { attribute: Object },
  data(){
    return {
      tempColor:''
    }
  },
  methods:{
    addColor(){
      this.attribute.pieColor.push(this.tempColor)
    },
    delColor(index){
      this.attribute.pieColor.splice(index, 1);
    }
  }
}
</script>

<style scoped>
.colorBlock{float: left; width: 25px;height: 25px;margin-right: 2px;}
.colorBlock:hover .delTag{display: inline-block}
.delTag{width: 100%;height: 100%;display: none;text-align: center;font-size: 18px;color: #fff}
</style>
